.button {
  padding: 11px 0;

  display: inline-block;

  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: $font-oswald;
  text-transform: uppercase;
  text-decoration: none;
  vertical-align: middle;
  color: #ffffff;

  background-color: $secondary-bacground;

  &:hover {
    background-color: #5eaa2f;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
